@import '~node_modules/modern-normalize/modern-normalize.css';

@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@500;700&family=Titan+One&display=swap');

/* Default styles*/

html {
  box-sizing: border-box;
  overflow-x: hidden;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style-type: none;
  padding-inline-start: 0;
  padding: 0;
  margin: 0;
}

svg {
  display: inline-block;
}

address {
  font-style: normal;
}
